import React, { useState, useEffect } from "react";
import Axios from "axios";
import Comment from "./Comment"
import Details from "./Details"
import Author from "./Author"

export default function ItemDetails(props) {
    const id = props.match.params.id;
    const [details, setDetails] = useState({});
    const [comments, setComments] = useState([]);
    const [author,setAuthor]=useState([]);
    let array1=[]


    useEffect(() => {
        Axios.get("https://jsonplaceholder.typicode.com/posts/" + id).then(
            (response) => {
                const data = response.data;
                setDetails(data);
            }
        );
    }, []);

    useEffect(() => {
        if (details.id && comments.length == 0) {
            Axios.get(
                "https://jsonplaceholder.typicode.com/posts/" + id + "/comments"
            ).then((response) => {
              setComments(response.data)
            });
        }
    },[details]);
array1= comments.filter((check) =>{
  return !(check.id != id)
})

console.log("array1",array1);
    useEffect(() => {
        if (details.id && author.length == 0) {
            Axios.get(
                "https://jsonplaceholder.typicode.com/users/" + id
            ).then((response) => {
              setAuthor(response.data);
            });
        }

    },[comments]);

    return (
        <div className="container">
        < Details title={details.title} id={details.id} body={details.body} />
      <Comment name={array1[0].name} email={array1[0].email} body={array1[0].body}/>
        <Author name={author.name} username={author.username} email={author.email} />
        </div>
    );
}
